import { Component, Vue} from 'vue-property-decorator'

@Component
export default class InfoWindow extends Vue {
    shouldShow: boolean = false;
    infoTitle: string = 'Title';
    infoContent: string = 'Infos';

    mounted(){
        this.$root.$on('toggle-info', () => {
            this.shouldShow = !this.shouldShow;
        });

        this.$root.$on('set-info', (data: {title: string, info: string}) => {
            this.shouldShow = false;
            this.infoTitle = data.title;
            this.infoContent = data.info;
        })
    }
}
